.content {
	width: 100%;
	max-width: 1600px;
	margin: 0 auto;
	position: relative;
	@include min(992) {
		transition: .3s ease; }


	&-wrap {
		@extend %offset;
		padding: {
			top: 271px; }
		@include m(992) {
			padding-top: 100px; }
		@include m(576) {
			padding-top: 80px; }


		&.padding-t--sm {
			padding-top: 150px;
			@include m(992) {
				padding-top: 100px; }
			@include m(576) {
				padding-top: 80px; } } } }


