.blur-filter {
    // filter: blur(2px)
    overflow: hidden;
    @include m(992) {
      overflow: auto; } }


.popup {
  width: 380px;
  padding: 40px;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translate(-50%,-200%) scale(.5);
  background: $c-bgc;
  text-align: center;
  z-index: 200;
  transition: all .5s ease;
  box-shadow: 0px 0px 5px .5px darken($c-accent, 5%);
  @include m(992) {
    padding: 16px; }
  @include m(768) {
    width: 290px; }

  @include m(480) {
    width: 250px; }



  &.is-active {
    top: 50%;
    transform: translate(-50%, -50%) scale(1);
    @include m(480) {
      top: 10%;
      transform: translate(-50%, 0) scale(1); } }


  &__title {
    color: $c-accent;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 24px;
    @include m(576) {
      font-size: 20px; } }

  &__field {
    border: 0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid darken($c-accent, 20%);
    padding: 14px 10px;
    width: 200px;
    outline: none;
    color: darken($c-accent, 20%);
    border-radius: 24px;
    transition: 0.25s;
    @include m(992) {
      margin: 10px auto;
      &:focus {
        width: 240px; } }
    @include m(480) {
      margin: 8px auto;
      &:focus {
        width: 200px; } }

    &:focus {
      width: 280px;
      border-color: $c-accent; } }


  &__esc-btn {
    position: absolute;
    right: 0;
    top: 0;
    width: 30px;
    height: 30px;
    border-radius: 30px;
    transform: translate(50%, -50%);
    background-color: darken($c-accent, 10%); }

  &__btn {
    border: 0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid $c-accent;
    padding: 14px 40px;
    outline: none;
    color: white;
    border-radius: 24px;
    transition: 0.35s ease;
    cursor: pointer;
    @include min(1024) {
      &:hover {
        background: $c-accent;
        color: #fff; } }
    @include m(992) {
      margin: 10px auto;
      padding: 10px 40px; } } }

.overlay-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  z-index: 150;
  opacity: 0;
  background-color: darken($c-accent, 40%);
  &.is-active {
    opacity: .7;
    width: 100vw;
    height: 100vh; } }




